
import * as Comlink from 'comlink';
import * as pl from './pkg/polars.js'

async function initHandlers() {
  await pl.default();
  await pl.init_hooks();

  await pl.initThreadPool(navigator.hardwareConcurrency);
  console.log(pl.Series.new_bool("wkr", [false]).as_str())
  function seriesWrap(ptr) {
    return pl.Series.__wrap(ptr)
  }
  function as_str(ptr) {
    return seriesWrap(ptr).as_str()
  }
  function new_bool(name, values) {
    return pl.Series.new_bool(name, values)
  }
  function new_f64(name, values) {
    return pl.Series.new_f64(name, values)
  }
  function df() {
    console.time('"DF"')
    const randomArr = () => Array.from({length: 100_000}, () => Math.round((Math.random() * 100) % 20 ));
    const arrs = Array.from({length: 100}, randomArr).map((arr, idx) => pl.Series.new_f64(`col_${idx}`, arr))
    // const st = s.as_str()
    // console.log(st)
    console.timeEnd('"DF"')
    return pl.DataFrame.read_columns(arrs[Symbol.iterator]());

  }
  async function df_unique({ptr}) {
    console.time('df_unique')
    let df = pl.DataFrame.__wrap(ptr);
    df = df.unique(false, ['col_1', 'col_2', 'col_3'], 'first')
    console.timeEnd('df_unique')
    return df.as_str()
    // return pl.DataFrame.__wrap(ptr).unique(false, undefined, 'first')
  }
  return Comlink.proxy({new_bool, new_f64, seriesWrap, as_str, df, df_unique})
}
// await initHandlers()




Comlink.expose({pl: initHandlers()})
